 /*eslint-disable*/ 
import React, { Component, useState, useEffect } from 'react';
import './App.css';
import { useHistory, useParams } from 'react-router-dom';
import { Nav, } from 'react-bootstrap';


function Detail( props ){
      let history = useHistory();  //  이제 history 라는 변수엔 큰 object {} 자료가 하나 저장이 되어있습니다.
                                   //  그 object 안에는 페이지 이동 내역 + 유용한 함수가 저장되어있습니다.
      let { id } = useParams();                           
      let [show, showchan] = useState(false);         
      let find = props.shoes.find( (item) => { 
                        return item.id == id;           
                  } );     
      let [alert, alertchan] = useState( true );                      

      useEffect( () => { 
             let timer = setTimeout( () => { alertchan(false) } , 2000 );
             console.log('hi');
             return () => { clearTimeout(timer); }
            }, [] );     
  
      return(
         <div className="container">
            <div className="row">
                { 
                    alert === true
                    ? <h3 className="aleat_t">재고가 얼마 남지 않았습니다.</h3>
                    : null              
                }                         
                <div className="col-md-6 prod">
                    <img src={ 'https://codingapple1.github.io/shop/shoes' + (find.id + 1) + '.jpg'  }  width="90%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{ find.title }</h4>
                    <p>{ find.content }</p>
                    <p>{ find.price }원</p>
                    <button className="btn btn-danger" onClick={ () => { showchan(!show) } } >주문하기</button> 
                    <button className="btn btn-danger" onClick={ () => { history.goBack(); } } >뒤로가기</button> 
                    <button className="btn btn-danger" onClick={ () => { history.push('/'); } } >장바구니</button>             
                </div>
            </div>
            {
                show === true 
                ? <div className="popup">                                          
                        <select name='color'>
                            <option>색상</option>
                            <option>보라</option>
                            <option>핑크</option>
                            <option>베이지</option>
                            <option>블랙</option>
                        </select>
                        <select >
                            <option>사이즈</option>
                            <option>230</option>
                            <option>240</option>
                            <option>250</option>
                            <option>260</option>
                            <option>270</option>
                        </select>
                        <div className="total"> 
                            <h3></h3>
                            <h3><span className="s1">합계 :</span><span className="s2">{ find.price }원</span></h3>                            
                        </div>
                    </div>      
                : null     
            }

            <Nav variant="pills" defaultActiveKey="/home">
                <Nav.Item>
                    <Nav.Link href="/home">Active</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1">Option 2</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="disabled" disabled>
                    Disabled
                    </Nav.Link>
                </Nav.Item>
            </Nav>
         </div>    
      );
  }

export default Detail;